
* {
  box-sizing: border-box;
  list-style: none;
  margin: 0;
  padding: 0;
  text-decoration: none;
  vertical-align: baseline;
}
a {
  color: #888888;
}
body {
  background: linear-gradient(60deg, #2f4f4f, #284343);
}
#app {
  color: #2c3e50;
  font-family: monospace;
  min-height: 98vh;
  padding: 8px 0 16px;
}
#app .header {
    color: #fff;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    padding: 8px 16px 0;
}
#app .loading {
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    color: #fff;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    font-size: 2em;
    height: 70vh;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
#app .filters {
    color: #fff;
    padding: 16px 16px 0;
}
#app .filters .counter {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
}
#app .filters .filter {
      display: inline-block;
      word-break: break-all;
}
#app .filters .filter-item {
      margin-left: 8px;
}
#app .opportunities {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    padding: 16px 16px 0;
    width: 100%;
}
#app .opportunities .opportunity-item {
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      background: #d5eaea;
      border-radius: 4px;
      box-shadow: 3px -3px 1px 0px #888888;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-flex: 0;
          -ms-flex: 0 1 280px;
              flex: 0 1 280px;
      height: 172px;
      margin-right: 6px;
      margin-top: 12px;
      overflow: hidden;
      padding: 8px;
      white-space: nowrap;
}
#app .footer {
    color: #fff;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    padding: 16px 16px 0;
}



